@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;500&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  scroll-behavior: smooth;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.a {
  height: 100vh;
  display: flex;
  align-items: center;
}

.a-left {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  /* background-color: aqua; */
  height: 100%;
}

.a-right {
  flex: 1 1;
  display: flex;
  justify-content: space-around;

  flex-direction: column;
  height: 50%;
}

.a-card {
  width: 60%;
  height: 70vh;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
  box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}

.a-card.bg {
  position: absolute;
  top: 50px;
  left: 50px;
  background-color: rgb(224, 169, 102);
}

.a-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (max-width: 480px) {
  .a {
    flex-direction: column;
    text-align: center;
    margin-top: 50px;
  }
  .a-left {
    width: 100%;
  }
  .a-card {
    height: 30vh;
  }
  .a-sub {
    padding: 0 20px 0 20px;
  }
  .a-card.bg {
    display: none;
  }
}

.c {
  height: 100vh;
  position: relative;
}

.c-bg {
  width: 20px;
  height: 100%;
  background-color: antiquewhite;
  position: absolute;
}

.c-wrapper {
  display: flex;
  padding: 50px;
}

.c-left {
  flex: 1 1;
}

.c-right {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.c-title {
  font-size: 60px;
  width: 80%;
}

.c-info-item {
  display: flex;
  align-items: center;
  margin: 50px 0px;
  font-weight: 300;
  width: 70%;
}

.c-icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.c-desc {
  font-weight: 300px;
}

form {
  margin-top: 20px;
}

input {
  width: 50%;
  font-family: poppins;
  font-weight: 300;
  height: 50px;
  border: none;
  border-bottom: 1px solid gainsboro;
  margin: 10px 0;
  font-size: 14px;
  padding-left: 10px;
}

textarea {
  width: 100%;
  font-family: poppins;
  resize: none;
  font-weight: 300;
  margin: 10px 0;
  font-size: 14px;
  padding-left: 10px;
  padding-top: 10px;
  border: 1px solid gainsboro;
}

button {
  border: none;
  padding: 15px;
  font-family: poppins;
  background-color: #e0a966;
  color: white;
  
  cursor: pointer;
  font-weight: 500;
}

.c-done {
  margin-top: 15px;
}

@media screen and (max-width: 480px) {
  .c {
    height: 100%;
  }
  .c-wrapper {
    flex-direction: column;
  }
}

.i {
  display: flex;
  height: 100vh;
  /* background-color: lightblue; */
}

.i-left {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.i-right {
  flex: 1 1;
  position: relative;
}

.i-left-wrapper {
  padding: 50px;
  height: 50%;
  /* background-color: aquamarine; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i-intro {
  font-size: 30px;
  font-weight: 300;
}

.i-name {
  font-size: 60px;
}

.i-title {
  height: 50px;
  overflow: hidden;
}

.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: rgb(224, 169, 102);
  /* background-color: bisque; */
  display: flex;
  align-items: center;
}

.i-title-wrapper {
  /* height will be 50px base on i-title */
  height: 100%;
  -webkit-animation: move 10s ease-in-out infinite alternate;
          animation: move 10s ease-in-out infinite alternate;
}

@-webkit-keyframes move {
  25% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  50% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  75% {
    -webkit-transform: translateY(-150px);
            transform: translateY(-150px);
  }
  100% {
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }
}

@keyframes move {
  25% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  50% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  75% {
    -webkit-transform: translateY(-150px);
            transform: translateY(-150px);
  }
  100% {
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }
}

.i-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  border-radius: 0px 0px 5px 4px;
  box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}

@media screen and (max-width: 480px) {
  .i {
    flex-direction: column;
  }
  .i-desc {
    display: none;
  }
  .i-left-wrapper {
    padding: 10px;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100%;
  }
}

.pl {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.pl-texts {
  width: 65%;
}

.pl-title {
  font-size: 50px;
  font-weight: 600;
}

.pl-desc {
  margin: 20px 0px;
}

.pl-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media screen and (max-width: 480px) {
  .pl {
    margin-top: 30px;
    padding: 10px;
  }

  .pl-title {
    font-size: 40px;
  }

  .pl-desc {
    display: none;
  }
}

.p {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 242);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
  box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}

.p-browser {
  height: 20px;
  background-color: rgb(243, 242, 242);
  display: flex;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  z-index: 2;
}

.p-circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: rgb(238, 217, 192);
  margin: 5px;
}

.p-img {
  width: 100%;
  transition: all 10s ease-in-out;
}

.p:hover .p-img {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

@media screen and (max-width: 480px) {
  .p {
    width: 40%;
    height: 23vh;
  }
}

.t {
  width: 50px;
  height: 25px;
  border-radius: 20px;
  border: 1px solid gray;
  top: 10px;
  right: 10px;
  background-color: white;
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 999;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.t-icon {
  width: 15px;
  height: 15px;
}

.t-button {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  position: absolute;
  cursor: pointer;
  background-color: #e0a966;
}

